import { useState } from 'react';
import { Button, MenuItem, Select, FormControl, InputLabel } from '@material-ui/core';
import { exportByMonth } from './api-exports'; // Assuming you have an API file for making requests
import Fraction from 'fraction.js';
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';

const ExportByMonth = () => {
    const [month, setMonth] = useState('');

    const decToFrac = (decimal) => {
        const maxDenominator = 10000;
        const fraction = new Fraction(decimal).toFraction(maxDenominator);
        return fraction.toString();
    }

    const handleData = (data) => {
        if (!data || data.length === 0) {
            alert('Khng c d liu  xut');
            return;
        }
        const headerMapping = {
            orderId: 'Phiu yu cu',
            customerName: 'Tn khch hng/n v',
            address: 'a ch',
            content: 'Ni dung',
            publishDates: 'Ngy ng',
            productType: 'Loi bo',
            size: 'Kch thc',
            quantity: 'S lng',
            totalPrice: 'Tng tin'
        };
    
        const formattedData = data.map((item) => {
            // Format publishDates array
            const formattedDates = item.publishDates?.map((date) => {
                const [year, month, day] = date.split('T')[0].split('-');
                const formattedDate = `${day}/${month}/${year}`;
                return formattedDate;
            }).join(', '); // Join formatted dates into a string

            // format size
            item.size = decToFrac(item.size ? item.size.$numberDecimal : 0);
    
            return {
                'Phiu yu cu': item.orderId,
                'Tn khch hng/n v': item.customerName,
                'a ch': item.address,
                'Ni dung': item.content,
                'Ngy ng': formattedDates,
                'Loi bo': item.productType,
                'Kch thc': item.size,
                'S lng': item.quantity,
                'Tng tin': item.totalPrice
            };
        });
    
        const worksheet = XLSX.utils.json_to_sheet(formattedData, { header: Object.values(headerMapping) });
        const workbook = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');
        const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
        const dataFile = new Blob([excelBuffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8' });
        saveAs(dataFile, 'thongkethang' + month + '.xlsx');
    };    

    const handleExport = async () => {
        try {
            const data = await exportByMonth(month);
            handleData(data);
        } catch (error) {
            console.error('Error exporting data:', error);
            // Handle error as needed
        }
    };

    return (
        <div style={{margin: '10px'}}>
            <FormControl variant="outlined" style={{ marginRight: '10px', width: '200px' }}>
                <InputLabel id="month-label">Chn thng</InputLabel>
                <Select
                    labelId="month-label"
                    id="month-select"
                    value={month}
                    onChange={(e) => setMonth(e.target.value)}
                    label="Month"
                >
                    <MenuItem value="01">Thng 1</MenuItem>
                    <MenuItem value="02">Thng 2</MenuItem>
                    <MenuItem value="03">Thng 3</MenuItem>
                    <MenuItem value="04">Thng 4</MenuItem>
                    <MenuItem value="05">Thng 5</MenuItem>
                    <MenuItem value="06">Thng 6</MenuItem>
                    <MenuItem value="07">Thng 7</MenuItem>
                    <MenuItem value="08">Thng 8</MenuItem>
                    <MenuItem value="09">Thng 9</MenuItem>
                    <MenuItem value="10">Thng 10</MenuItem>
                    <MenuItem value="11">Thng 11</MenuItem>
                    <MenuItem value="12">Thng 12</MenuItem>
                </Select>
            </FormControl>
            <Button
                variant="contained"
                color="primary"
                onClick={handleExport}
            >
                Xut
            </Button>
        </div>
    );
};

export default ExportByMonth;
